@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap');

.Home {
    background-color: white;
    /*height: 100vh;*/
    font-family: "Noto Sans", sans-serif;
    /*padding-bottom: 200px;*/
}

.backDrop {
    /* border: 1px solid rgb(235, 235, 235); */
    width: 700px;
    height: 250px;
    margin: 10px auto;
    position: relative;
    z-index: 5;
    background-color: rgba(19,185,85, 0.1);
    overflow: hidden;
    border-radius: 5px;
}
.backDrop h2 {
    padding-top: 10px;
    color: black;
    border-radius: 5px;
}
.backDrop h2 span {
    color: rgb(19,185,85);
}
.tag {
    margin: 0 auto 0 auto;
    font-size: 14px;
    color: black;
    font-weight: 700;
    width: 400px;
}
.circle1 {
    width: 150px;
    height: 150px;
    border-radius: 100%;
    background-color: rgba(19,185,85, 0.125);
    position: absolute;
    top: 5px;
    right: 5px;
}
.circle2 {
    width: 220px;
    height: 220px;
    border-radius: 100%;
    background-color: rgba(19,185,85, 0.125);
    position: absolute;
    top: 70px;
    right: 70px;
}
.circle3 {
    width: 150px;
    height: 150px;
    border-radius: 100%;
    background-color: rgba(19,185,85, 0.125);
    position: absolute;
    top: 20px;
    right: 480px;
}
.circle4 {
    width: 75px;
    height: 75px;
    border-radius: 100%;
    background-color: rgba(19,185,85, 0.125);
    position: absolute;
    top: 100px;
    right: 430px;
}
.circle5 {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    background-color: rgba(19,185,85, 0.125);
    position: absolute;
    top: 180px;
    right: 520px;
}
.desc {
    margin: 50px 0 0 0;
    font-size: 14px;
    font-weight: bold;
    color: black;
}

.HomeItemContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: auto;
    width: 900px;
    
}

@media (max-width: 800px) {
    .backDrop {
        width: 90%;
    }
    .tag {
        width: 80%;
        font-size: 12px;
    }
    .HomeItemContainer {
        width: 90%;
    }
    .desc {
        font-size: 12px;
        margin: 50px 30px 10px 30px;
    }
}